export default {
	data() {
		return {
			isLoading: true,
			teamMsg: [],
			totalNumber: null,
			modelName: '',
			name: '',
			phone: '',
			isNull: false,
			takeNumber: '',
			page: 1,
			stopLoad: false,
			codePic:null,
			shopTeams:[],
			shopName:null
		}
	},
	onLoad(options) {
		this.shopName = options.shop_name
		this.getShopTeam()
	},
	methods: {
		getShopTeam(){
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/userCenter/myteam",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					page: this.page
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					this.isLoading = false
					if (res.data.status == 0) {
						this.shopTeams = res.data.data.users
						this.totalNumber = res.data.data.count
						return
					}
					this.showToast(res.data.msg)
				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		},
		takePhone(num) {
			this.takeNumber = num
			this.modelName = 'phoneModel'
		},
		closeModel() {
			this.modelName = ''
		},
		
		sureTakePhone() {
			uni.makePhoneCall({
				phoneNumber: this.takeNumber
			});
			this.closeModel()
		},

		toMemberDetail(user_id,shop_id) {
			uni.navigateTo({
				url: "../memberMessage/memberMessage?user_id=" + user_id+"&shop_id="+ shop_id
			})
		},
		
		loadMore() {
			if (this.stopLoad) {
				return
			}
			this.page++
			uni.request({
				url: this.websiteUrl + "/api/userCenter/myteam",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					page: this.page,
					offset: 20
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						if (res.data.data.users.length == 0) {
							this.stopLoad = true
							return
						}
						for (var i = 0; i < res.data.data.users.length; i++) {
							this.shopTeams.push(res.data.data.users[i])
						}
					}
				}
			})
		},
		//处理下单时间
		handleTime(time) {
			let date = new Date(time * 1000)
			let Y = date.getFullYear().toString()
			let M = (date.getMonth() + 1).toString().padStart(2, '0')
			let D = date.getDate().toString().padStart(2, '0')
			let h = date.getHours().toString().padStart(2, '0')
			let m = date.getMinutes().toString().padStart(2, '0')
			let s = date.getSeconds().toString().padStart(2, '0')
			let newTime = Y + '-' + M + '-' + D + ' ' + h + ':' + m
			return newTime
		},
	}
}
